import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from "react-bootstrap-icons";
import headerImg from "../assets/images/header-img.jpg";
import { useState, useEffect } from "react";
import "animate.css";
import TrackVisibility from "react-on-screen";

export const Banner=({onUpdateActiveLink})=>{
    const [loopNum, setLoopNum]=useState(0);
    const [isDeleting, setIsDeleting]=useState(false);
    const toRotate=[" JavaScript", " jQuery", " React"];
    const [text, setText]=useState('');
    const [delta, setDelta]=useState(300-Math.random()*100);
    const period=2000;

    useEffect(()=>{
        let ticker= setInterval(()=>{
            tick();
        }, delta);

        return () => {clearInterval(ticker)};
    }, [text])

    const tick=()=>{
        let i=loopNum % toRotate.length;
        let fullText=toRotate[i];
        let updatedText=isDeleting ? fullText.substring(0, text.length-1) : fullText.substring(0, text.length+1);

        setText(updatedText);

        if(isDeleting){
            setDelta(prevDelta => prevDelta/2)
        }

        if(!isDeleting && updatedText==fullText){
            setIsDeleting(true);
            setDelta(period);
        }else if(isDeleting && updatedText==''){
            setIsDeleting(false);
            setLoopNum(loopNum +1);
            setDelta(400);
        }
    }
    return(
    <section className="banner" id="home">
        <Container>
            <Row className="align-items-center">
                <Col xs={12} md={6} xl={7}>
                    <TrackVisibility>
                    {({isVisible})=>
                        <div  className={isVisible ? "animate__animated animate__fadeIn " : ""}>
                            <span className="tagline">Welcome to my Portfolio</span>
                            <h1>{`Hi, I'm a beginner Front-End developer, who enjoys`}<span className="wrap">{text}</span></h1>
                            <p>Hello, my name is Khrystyna. I am a Front-End Developer located in Slovakia. 
                                I have serious passion for UI effects, and creating intuitive, dynamic user experiences.</p>
                                <p>So, let's dive into the sea of Web Development :)</p>
                            <button className="vvd"><a href="#connect" onClick={() => onUpdateActiveLink('connect')}>Let's connect <ArrowRightCircle size={25}/></a></button>
                        </div>}
                    </TrackVisibility>
                </Col>
                <Col xs={12} md={6} xl={5}>
                    <img src={headerImg} alt="header"/>
                </Col>
            </Row>
        </Container>
    </section>)
}